/* css global variables */
:root {
	--dark: #151a1c;
	--light: #e9e9e9;
	--acc: red;

	/* typography colors */
	--txt-dark: black;
	--txt-light: white;
	--txt-acc: #dc9457;
	transform-style: preserve-3d;
}

* {
	text-decoration: none;
	box-sizing: border-box;

	margin: 0;
	padding: 0;
	z-index: 1;
}

html {
	--vh: 1vh;

	/* fixate viewport and prevent scroll */
	/* ************** */
	height: calc(100*1vh);
	height: calc(100*var(--vh));
	/* overflow: hidden; */
}

body {
	position: relative;

  	min-height: -webkit-fill-available;
	font-family: 'Signika', sans-serif;
	font-weight: 400;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;

	background-color: #000;
	transform-style: preserve-3d;
	z-index: 1;

	height: 100vh;

	overflow: hidden;
}
code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}

a {
	color: #fff;
}

#root {
	height: 100vh;
	width: 100%;
	overflow-x: hidden;
	overflow-y: scroll;

	scroll-behavior: smooth;
	/* perspective: 300px; */
	/* transform-style: preserve-3d; */
}

@media (min-width: 600px) {
	#root {
		scrollbar-width: thin;
	}
	::-webkit-scrollbar {
		width: 1rem;
	}
	::-webkit-scrollbar-track {
		background: #0c1113;
	}
	::-webkit-scrollbar-thumb {
		width: 1rem;
		border-radius: 1rem;
		background-color: rgb(3, 6, 7);
	}
	::-webkit-scrollbar-thumb:hover {
		width: 1rem;
		border-radius: 1rem;
		background-color: rgb(0, 0, 0);
	}
	.idle::-webkit-scrollbar-track,
	.idle::-webkit-scrollbar-thumb {
		background-color: rgba(0, 0, 0, 0);
	}
}



img {
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}

.scroll-lock #root {
	overflow: hidden;
}
/* General changes applied in consideration to device viewport */

:root {
	/* scale everything with the viewport */
	font-size: calc(16px + (16 - 5) * (100vw - 500px) / (500 - 151));
}

@media screen and (min-width: 501px) {
   :root {
		font-size: calc(18px + (18 - 12) * (100vw - 1500px) / (1500 - 501));
		/* font-size: 16px; */
	}
}
