	position: absolute;
	flex-direction: row;
	align-items: center;
	top:0;
	width: 100%;
	padding: .2rem 1rem;
}
section {
	display: flex;
	flex-direction: column;
	/* justify-content: center; */
	align-items: center;
	width: 100%;

	padding: 4rem 0 7rem 0;

	background-color: var(--light);
}
section.dark {
	background-color: var(--dark);
}
section.accent {
