}

.Heading-Group {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	width: 90%;
	z-index: 2;
}
.left {
	align-items: flex-start;
	text-align: left;
	padding-right: 10%;
}
.right {
	align-items: flex-end;
	text-align: right;
	padding-left: 10%;
}

.Image-Wrapper {
	position: relative;
	height: 100%;
	width: 100%;
	font-size: 0;
}
.Image-Wrapper.overlay::after {
	content: '';
	position: absolute;
	top: 0; left: 0;
	height: 100%;
	width: 100%;

	background-color: rgba(1, 14, 19, 0.801);
	z-index: 2;
}

.Image-Wrapper img {
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.background {
	position: absolute;
	height: 100%;
	width: 100%;
	top: 0; left: 0;
	opacity: .75;
	object-fit: cover;

	z-index: -1;
}
.App {
	--stag-mod: 0;
}

.idle {
	pointer-events: none;
}
.idle >* {
	opacity: 0;
}
.idle.bottom >* {
	transform: translate3d(0, 4rem, 0);
}
.idle.right >* {
	transform: translate3d(4rem, 0, 0);
}
.idle.left >* {
	transform: translate3d(-4rem, 0, 0);
}

.va > :nth-child(1) {
	--stag: 0;

}
.va > :nth-child(2) {
	--stag: 1;
}
.va > :nth-child(3) {
	--stag: 2;
}
.va > :nth-child(4),
.Post-Group > :nth-child(2) > :nth-child(1) {
	--stag: 3;
}
.va > :nth-child(5),
.Post-Group > :nth-child(2) > :nth-child(2) {
	--stag: 4;
}
.va > :nth-child(6),
.Post-Group > :nth-child(2) > :nth-child(3) {
	--stag: 5;
}
.va > :nth-child(7),
.Post-Group > :nth-child(3) > :nth-child(1) {
	--stag: 6;
}
.va > :nth-child(8),
.Post-Group > :nth-child(3) > :nth-child(2) {
	--stag: 7;
}
.va > :nth-child(9),
.Post-Group > :nth-child(3) > :nth-child(3) {
	--stag: 8;
}

.va >* {
	transition: opacity .4s ease, transform .6s ease-out;
	will-change: opacity, transform;
	transition-delay: calc(.06s * calc(var(--stag) + var(--stag-mod)));
}



/* nav */
nav.down::before,
nav.up::before {
	/* top: 0; */
	transition: transform .75s cubic-bezier(0.395, 0.690, 0.690, 0.400) .1s;
	/* transition-property: transform, opacity; */
}
nav.down::before {
	/* opacity: 1; */
	transform: translate3d(0, -300vh, 0);
}
nav.up::before {
	top: -200vh;
	/* opacity: 1; */
	transform: translate3d(0, 300vh, 0);
}
nav {
	display: flex;
	position: -webkit-sticky;
	position: sticky;
	flex-direction: row;
	align-items: center;
	justify-content: center;
	top: 0;
	height: 2.4rem;
	width: 100%;
	z-index: 4;
	/* background-color: #151a1c; */
}



.Section-Links {
	display: flex;
	position: relative;
	align-items: center;
	height: 100%;
	margin-left: 10vw;
	/* width: calc(27rem + 10vw); */
	/* background-color: rgb(10, 10, 27); */
}

nav::before {
	content: '';
	position: absolute;
	height: 200vh;
	width: 100%;
	top: 100vh; left: 0;
	background-color: rgb(13, 19, 24);
	z-index: 2;
	transform: translate3d(0, 0, 0);
	will-change: transform;
}

.Quick-Bar {
	display: flex;
	position: absolute;
	flex-direction: row;
	align-items: center;
	height: 100%;
	width: 100%;
	padding: 0 1rem;

	/* transform: translate3d(0, -100%, 0); */

}
nav.stuck .Quick-Bar {
	background-color: #0c1113;
}
.Quick-Bar .Anchor,
.Quick-Bar button,
button.book {
	opacity: 0;

	transition: opacity .2s ease-in-out;
	transition-property: opacity;
	will-change: opacity;
}

nav.stuck .Quick-Bar .Anchor,
nav.stuck button.book {
	opacity: 1;
}
nav.stuck button.book {
	transition-delay: .1s;
}
nav.stuck .Quick-Bar button {
	opacity: .6;
	transition-delay: .2s;
}

